.subscription-primary__title {
  color: #000000;
  font-size: 18px;
  line-height: 28px;
  font-weight: 400;
  font-family: 'Montserrat-Regular';
  text-align: center;
  margin-bottom: 164px;
}
.subscription-primary__list {
  display: grid;
  grid-column-gap: 134px;
  grid-row-gap: 40px;
  justify-content: center;
  grid-template-columns: repeat(auto-fit, 260px);
  margin-bottom: 154px;
}
.subscription-primary__item {
  transition: all .3s;
  position: relative;
}
.subscription-primary__item::before {
  left: 44%;
  top: 49%;
  transform: translateX(-50%) translateY(-50%);
  position: absolute;
  content: "";
  background-image: url('../../assets/images/focus-price.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 380px;
  height: 374px;
  display: none;
}
.subscription-primary__item:hover::before {
  display: block;
}
.subscription-primary__item:hover .subscription-primary__bg {
  transform: scale(1.1);
}
.subscription-primary__bg {
  background-image: -moz-linear-gradient(45deg, #3591bc 11.597%, #62c990 45.386%, #01a730 82.983%, #01a730 89.307%);
  background-image: -webkit-linear-gradient(45deg, #3591bc 11.597%, #62c990 45.386%, #01a730 82.983%, #01a730 89.307%);
  height: 240px;
  width: 240px;
  transition: all .3s;
  position: relative;
}
.subscription-primary__bg::after {
  background-image: url('../../assets/images/shadow.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: absolute;
  content: "";
  bottom: -48px;
  left: -20px;
  right: 0;
  margin: 0 auto;
  width: 282px;
  height: 42px;
  z-index: 100;
}
.subscription-primary__mounth {
  position: absolute;
  left: 32px;
  top: 38px;
  font-weight: 500;
  font-family: 'Montserrat-Medium';
  font-size: 28px;
  line-height: 45px;
  color: #ffffff;
}
.subscription-primary__price {
  position: absolute;
  right: 30px;
  bottom: 38px;
  font-weight: 700;
  font-family: 'Montserrat-Bold';
  text-transform: uppercase;
  font-size: 38px;
  line-height: 45px;
  color: #ffffff;
}
.subscription-primary__title.primary__title-second {
  margin-bottom: 50px;
}
