.checkbox-wrapper {
  margin: 36px 0 28px;
  display: flex;
  align-items: center;
  cursor: pointer;
  transition: all .3s;
  max-width: fit-content;
  font-size: 18px;
  line-height: 28px;
  font-weight: 400;
  font-family: 'Montserrat-Regular';
}
.checkbox-wrapper:hover .checkbox-wrapper__text {
  color: #007bff;
}
.checkbox-wrapper__input {
  -webkit-appearance: none;
  position: absolute;
  z-index: -1;
}
.checkbox-wrapper .my-checkbox {
  margin-right: 12px;
  position: relative;
  width: 26px;
  height: 26px;
  border: 2px solid #dbdbdb;
  background-color: transparent;
  border-radius: 5px;
}
.checkbox-wrapper .checkmark {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 12px;
  height: 12px;
  background-color: #c76f8c;
  display: none;
}
.checkbox-wrapper input:checked ~ .my-checkbox .checkmark {
  display: block;
  background-color: #3c9bb8;
}
.checkbox-wrapper__text {
  font-weight: 400;
  font-family: 'Montserrat-Regular';
  color: #3c3c3c;
  font-size: 16px;
  line-height: 100%;
}
.card__label {
  display: block;
  margin-bottom: 18px;
  font-weight: 400;
  font-family: 'Montserrat-Regular';
  font-size: 18px;
  line-height: 28px;
}
.card__input.card-number {
  display: block;
